// React Package Module
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

// Constant
import { Box, styled, Typography } from "@mui/material";

// Content Component
import AuthorInfo from "components/contents/AuthorInfo";
import NovelCard from "components/contents/NovelCard";

// Constant
import { MESSAGE } from "common";

// API
import { getData } from "common/communication";

/** STYLE ì •ì˜ */
// ì „ì²´ ì˜ì—­
const Wrapper = styled(Box)({
	width: "100%",
	height:"100%",
	display: "flex",
  gap: 20,
});

// ì†Œì„¤ ì»´í¬ë„ŒíŠ¸ ì¹´ë“œ ì˜ì—­
const NovelCardBox = styled(Box)({
	flexGrow: 1,
  height: "100%",
	width: "80%",
	margin: "0 auto",
	display: "flex",
	flexWrap: "wrap",
  overflow: "scroll",
  paddingTop: 20,
  gap: 20,

    // ìŠ¤í¬ë¡¤ë°” ìˆ¨ê¸°ê¸°
    '&::-webkit-scrollbar': {
      display: 'none'
    },

    '& > div' : {
      margin: 0
    }

});



const IsDataInfo = styled(Typography)({
	fontSize: 17,
});

/** ì‘ê°€ ìƒì„¸ ì •ë³´ë¥¼ ë³´ì—¬ì£¼ëŠ” ëª¨ë‹¬ (ì†Œì„¤ ìƒì„¸ í˜ì´ì§€ì—ì„œ ì‘ê°€ ë‹‰ë„¤ì„ í´ë¦­ì‹œ í•´ë‹¹ íŒì—… ë„ì›Œì§) */
const AuthorDetailModal = (props) => {
	const [authorNovelData, setAuthorNovelData] = useState([]);   // ì‘ì„±í•œ ë©”ì¸ ì†Œì„¤ ë°ì´í„°
	const [userImg, setUserImg] = useState("");                   // ì‚¬ìš©ì í”„ë¡œí•„ ì´ë¯¸ì§€
	const [profile, setProfile] = useState(                       // ë¡œì»¬ìŠ¤í† ë¦¬ì§€ì— ì €ì¥ëœ ì‚¬ìš©ì ì •ë³´
		JSON.parse(localStorage.getItem("profile"))
	);

	const getNovelData = () => {
		getData("novel/getAuthorNovel", {
			created_user: props.authorId,
			login_id: profile.login_id,
		})
			.then(function (data) {
				setAuthorNovelData(data.novel_data);
				setUserImg(data.user_image);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	useEffect(() => {
		getNovelData();
	}, []);

	const navigate = useNavigate();

	const goToDetail = (novel) => {
		navigate("/novel_detail", { state: { props: novel } });
	};

	return (
		<Wrapper>
			<AuthorInfo
				authorNickName={props.authorNickName}
				authorId={props.authorId}
				user_image={userImg}
			/>
			<NovelCardBox>
				{authorNovelData.length !== 0 ? (
					authorNovelData.map((list) => {
						return (
							<NovelCard
								key={list.main_seqno}
								main_seqno={list.main_seqno}
								title={list.title}
								description={list.description}
								created_date={list.created_date}
								created_user={list.created_user}
								user_id={profile.login_id}       // ì—¬ê¸°ì„œ ë„˜ì–´ê°€ëŠ” user_idëŠ” í•´ë‹¹ ì†Œì„¤ ì°œ ì—¬ë¶€ë¥¼ ìœ„í•œ ê°’
								pick_yn={list.pick_yn}
								cover_image={list.cover_image}
								getNovelData={getNovelData}
								onClick={() => {
									goToDetail(list);
									props.closeModal();
								}}
							/>
						);
					})
				) : (
					<IsDataInfo>{MESSAGE.NO_INCOMPLETE_NOV}</IsDataInfo>
				)}
			</NovelCardBox>
		</Wrapper>
	);
};

export default AuthorDetailModal;
