// React Package Module
import { useEffect, useState, React } from "react";

// MUI Package Module
import { Box, styled, Typography } from "@mui/material";

// Control Component
import Buttons from "components/controls/Button";

// Constant
import { CODE, LABEL, COLOR } from "common";

// API
import { getData } from "common/communication";

/** STYLE ì •ì˜ */
// ì „ì²´ ì˜ì—­
const Wrapper = styled(Box)({
	width: "100%",
	display: "flex",
	flexDirection: "column",
	padding: "0 3%",
  	paddingRight: "1.5%",
	boxSizing: "border-box",
	paddingTop: "20px",
	height: "100%",
});

// ì œëª©, ì´ì–´ì“°ê¸° ë²„íŠ¼ ì˜ì—­
const HeaderBox = styled(Box)({
	width: "100%",
	display: "flex",
	boxSizing: "border-box",
  flexShrink: 0
});

const Title = styled(Typography)({
	marginBottom: 15,
	fontSize: 18,
	fontWeight: "bold",
});

const Content = styled(Typography)({
	fontSize: 15,
  whiteSpace: "pre-wrap",
  
});

const ScrollBox = styled(Box)({
  overflow: "auto",
	height: "100%",

  '&::-webkit-scrollbar': {
    width: 7, 
  },
  '&::-webkit-scrollbar-thumb': {
    background: '#aaa',
    borderRadius: 5,
  },
  '&::-webkit-scrollbar-track': {
    backgroundColor: 'transparent',
  },
})


/** ë¯¸ì™„ì„± ì‘í’ˆ (ë©”ì¸ ì†Œì„¤) ì½ê¸° ì»´í¬ë„ŒíŠ¸ (ì‘í’ˆ ìƒì„¸ í˜ì´ì§€ì—ì„œ view ë²„íŠ¼ í´ë¦­ ì‹œ ë¯¸ì™„ì„±ì‘ì¼ ê²½ìš° í•´ë‹¹ íŒì—… ë„ì›Œì¤Œ) */
const ViewIncompleteNovModal = (props) => {
	const [mainNovel, setMainNovel] = useState({});         // ë©”ì¸ ì†Œì„¤ ë°ì´í„°

	// ë¯¸ì™„ì„± ì†Œì„¤ ë³´ê¸°
	useEffect(() => {
		getData("novel/getMainNovel", { novel_seqno: props.main_seqno })
			.then(function (data) {
				// í˜„ì¬ íŒì—…ê³¼ ì†Œì„¤ ìƒì„¸ í˜ì´ì§€ì—ì„œ ê°’ì„ ê³µìœ í•´ì•¼ í•˜ë¯€ë¡œ 2êµ°ë° ë°ì´í„° ì„¸íŒ…
				setMainNovel(data[0]);

				// NovDetailì—ì„œ í•„ìš”í•œ ë°ì´í„°ë§Œ ë½‘ì•„ ê°€ê³µ í›„ ì „ë‹¬
				const mainData = {
					title: data[0].title,
					content: data[0].content,
					main_seqno: data[0].main_seqno,
				};
				props.setMainNovel(mainData);
			})
			.catch((err) => {
				console.log(err);
			});
	}, []);

	return (
		<Wrapper>
			<HeaderBox>
				<Title>{mainNovel && mainNovel.title}</Title>
				{mainNovel.created_user !== props.login_id && (
					<Buttons
						type={CODE.BUTTON.BASIC}
						backgroundColor={COLOR.WHITE}
						color={props.color === "#121212" ? COLOR.WHITE : COLOR.BLACK}
						name={LABEL.BUTTONS.GOTOWRITE}
						margin={"-30px 0px 0px auto"}
						padding={0}
						changeState={props.changeState}
					/>
				)}
			</HeaderBox>
			<ScrollBox>
				<Content>
					{mainNovel && mainNovel.content
						? mainNovel.content.split("\\n").map((line, i) => (
								<>
									{line}
									<br />
								</>
						  ))
						: ""}
				</Content>
			</ScrollBox>
		</Wrapper>
	);
};

export default ViewIncompleteNovModal;