// React Package Module
import { useEffect, useState } from "react";

// MUI Package Module
import { Box, styled, Typography } from "@mui/material";

// Control Component
import Buttons from "components/controls/Button";

// Constant
import { CODE, LABEL, COLOR, MESSAGE } from "common";

// API
import { getData } from "common/communication";

/** STYLE ì •ì˜ */
// ì „ì²´ ì˜ì—­
const Wrapper = styled(Box)({
	width: "100%",
	height: "100%",
	display: "flex",
	flexDirection: "column",
	padding: "0 3%",
	boxSizing: "border-box",
});

const WholeBox = styled(Box)({
	width: "100%",
	height: "100%",
	display: "flex",
	flexDirection: "column",
	gap: 10,
	marginTop: 20,
	boxSizing: "border-box",
});

// ì „ì²´ íƒœê·¸ ì˜ì—­
const TagBox = styled(Box)({
	flex: 1,
	height: "100%",
	boxSizing: "border-box",
});

// ì„ íƒëœ íƒœê·¸ í‘œì‹œ ì˜ì—­
const SelectedTagBox = styled(Box)({
	flex: 1,
	height: "100%",
	padding: 10,
	boxSizing: "border-box",
});

// ì¥ë¥´ íƒœê·¸ ì˜ì—­
const GenreBox = styled(Box)({
	padding: 10,
	boxSizing: "border-box",
	width: "100%",
});

// í‚¤ì›Œë“œ íƒœê·¸ ì˜ì—­
const KeywordBox = styled(Box)({
	padding: 10,
	boxSizing: "border-box",
});

// ìƒë‹¨ ì•ˆë‚´ ë©”ì„¸ì§€ ì˜ì—­
const IntroMsg = styled(Typography)({
	fontSize: 18,
	fontWeight: "bolder",
	textAlign: "center",
	marginBottom: -5,
});

// ì¥ë¥´, í‚¤ì›Œë“œ êµ¬ë¶„ í…ìŠ¤íŠ¸
const DivTag = styled(Typography)({
	fontSize: 14,
	marginBottom: 10,
});

/** ì„œë¸Œ ì†Œì„¤ ì‘ì„± í›„ íƒœê·¸ ì„ íƒí•˜ëŠ” íŒì—… */
const SelectTagModal = (props) => {

	const [tag, setTag] = useState([]);             // ì „ì²´ íƒœê·¸
	const [genre, setGenre] = useState([]);         // ì„ íƒëœ ì¥ë¥´
	const [keyword, setKeyword] = useState([]);     // ì„ íƒëœ í‚¤ì›Œë“œ

	useEffect(() => {
		getData("common/tag")
			.then((data) => {
				setTag(data);
			})
			.catch((err) => {
				console.log(err);
			});
	}, []);

  // íƒœê·¸ í´ë¦­ ì‹œ ì‹¤í–‰ë  í•¨ìˆ˜ (íƒœê·¸ ì„ íƒ ë° ì„ íƒ í•´ì œ)
  const onSelectTags = (tag, type) => {
    const checkState = type === "genre" ? genre : keyword;          // í˜„ì¬ ì„ íƒëœ íƒœê·¸ë“¤ì„ ë‹´ëŠ” ë°°ì—´
    const setState = type === "genre" ? setGenre: setKeyword;       // ìƒíƒœë¥¼ ì—…ë°ì´íŠ¸í•˜ëŠ” í•¨ìˆ˜
    // findIndex ë§¤ì„œë“œë¥¼ ì‚¬ìš©í•˜ì—¬ trueë¥¼ ë°˜í™˜í•˜ëŠ” ì²« ë²ˆì§¸ ìš”ì†Œì˜ ì¸ë±ìŠ¤ë¥¼ ë‹´ì€ ë°°ì—´ ìƒì„±
    const check = checkState.findIndex((v) => v === tag);           // ì„ íƒëœ íƒœê·¸ì™€ ì¼ì¹˜í•˜ëŠ” ìš”ì†Œì˜ ì¸ë±ìŠ¤ ì¶”ì¶œí•˜ì—¬ ë‹´ëŠ” ë°°ì—´
    const maxLength = type === "genre" ? 1 : 2;                     // ì„ íƒ ê°€ëŠ¥í•œ ìµœëŒ€ ê°¯ìˆ˜

    // ì‚¬ìš©ìê°€ ì„ íƒí•œ íƒœê·¸ê°€ ê¸°ì¡´ ì„ íƒëœ íƒœê·¸ ë°°ì—´ì— ì—†ëŠ” ê²½ìš°
		if (check === -1) {
      // ì„ íƒëœ íƒœê·¸ ë°°ì—´ì˜ ê°¯ìˆ˜ì™€ ìµœëŒ€ í—ˆìš© ê°¯ìˆ˜ ë¹„êµí•˜ì—¬ ì´ˆê³¼ì¼ ê²½ìš° ì•Œë¦¼ì°½ ë„ì›Œì£¼ê³  í•¨ìˆ˜ ì¢…ë£Œ
      if (checkState.length > maxLength) {
        alert(type === "genre" ? MESSAGE.OVER_SELECTED_GENRE : MESSAGE.OVER_SELECTED_KEY_WORD);
        return;
        // ì´ˆê³¼í•˜ì§€ ì•Šì„ ê²½ìš° ìŠ¤í”„ë ˆë“œ ì—°ì‚°ì ì‚¬ìš©í•˜ì—¬ ê¸°ì¡´ ë°°ì—´ ì²«ë²ˆì§¸ ìë¦¬ì— ì‚¬ìš©ìê°€ ì„ íƒí•œ íƒœê·¸ ì¶”ê°€ 
      } else {
        setState([tag, ...checkState]);
      }
    // ì‚¬ìš©ìê°€ ì„ íƒí•œ íƒœê·¸ê°€ ê¸°ì¡´ ì„ íƒëœ íƒœê·¸ ë°°ì—´ì— ì´ë¯¸ ì¡´ì¬í•˜ëŠ” ê²½ìš° ì„ íƒ í•´ì œ
    } else {
      // ì‚¬ìš©ìê°€ ì„ íƒí•œ íƒœê·¸ë¥¼ ê¸°ì¡´ ë°°ì—´ì—ì„œ ì°¾ì€ ê²½ìš° ì‚¬ìš©ìê°€ ì„ íƒí•œ íƒœê·¸ë¥¼ ì œì™¸í•œ ìƒˆë¡œìš´ ë°°ì—´ ìƒì„±
      setState(checkState.filter((v) => v !== checkState[check]));
    }
	};

	// ì €ì¥ í›„ ë‹¤ìŒ ë²„íŠ¼ í´ë¦­ ì‹œ ì‹¤í–‰í•  ê¸°ëŠ¥ë“¤ í•¨ìˆ˜
	const onClickNextBtn = () => {
		if (genre.length === 0 || keyword.length === 0) {
			alert(MESSAGE.MIN_SELECT_TAG);
		} else {
			props.changeState();
			props.setTags({
				genre: genre,
				keyword: keyword,
			});
		}
	};
	
	return (
		<Wrapper>
			<Buttons
				type={CODE.BUTTON.BASIC}
				backgroundColor={COLOR.WHITE}
				color={props.color === "#121212" ? COLOR.WHITE : COLOR.BLACK}
				name={LABEL.BUTTONS.GOTONEXT}
				margin={"5px 20px 5px auto"}
				onClickNextBtn={onClickNextBtn}
			/>
			<IntroMsg>{MESSAGE.SELECT_TAG_INTRO}</IntroMsg>
			<WholeBox>
				<TagBox>
					<GenreBox>
						<DivTag>{LABEL.SELECT.GENRE}</DivTag>
						{tag
							.filter((list) => list.group_code === "GENRE")
							.map((list, i) => {
								return (
									<Buttons
										key={i}
										type={CODE.BUTTON.TAG}
										name={list.code_name}
										backgroundColor={`#${list.color}`}
										onSelectTags={() => onSelectTags(list, "genre")}
									/>
								);
							})}
					</GenreBox>
					<KeywordBox>
						<DivTag>{LABEL.SELECT.KEY_WORD}</DivTag>
						{tag
							.filter((list) => list.group_code === "KEYWORD")
							.map((list, i) => {
								return (
									<Buttons
										key={i}
										type={CODE.BUTTON.TAG}
										name={list.code_name}
										backgroundColor={`#${list.color}`}
										onSelectTags={() => onSelectTags(list, "keyword")}
									/>
								);
							})}
					</KeywordBox>
				</TagBox>
				<SelectedTagBox>
					{genre.map((tag, i) => {
						return (
							<Buttons
								key={i}
								type={CODE.BUTTON.TAG}
								name={tag.code_name}
								backgroundColor={`#${tag.color}`}
							/>
						);
					})}
					{keyword.map((tag, i) => {
						return (
							<Buttons
								key={i}
								type={CODE.BUTTON.TAG}
								name={tag.code_name}
								backgroundColor={`#${tag.color}`}
							/>
						);
					})}
				</SelectedTagBox>
			</WholeBox>
		</Wrapper>
	);
};

export default SelectTagModal;