// React Package Module
import { useEffect, useState } from "react";

// Control Component
import Buttons from "components/controls/Button";
import Inputs from "components/controls/Input";

// MUI Package Module
import { Box, styled, Typography } from "@mui/material";

// Constant
import { CODE, LABEL, COLOR, MESSAGE } from "common";

/** STYLE ì •ì˜ */
// ì „ì²´ ì˜ì—­
const Wrapper = styled(Box)({
	width: "100%",
	height: "100%",
	display: "flex",
	flexDirection: "column",
	padding: "0 3%",
	boxSizing: "border-box",
	marinTop: "-30px",
});

const WholeBox = styled(Box)({
	width: "100%",
	height: "88%",
	display: "flex",
	gap: 20,
	marginTop: 20,
	boxSizing: "border-box",
});

const ScrollBox = styled(Box)({
  overflow: "auto",
	height: "100%",

  '&::-webkit-scrollbar': {
    width: 5, 
  },
  '&::-webkit-scrollbar-thumb': {
    background: '#aaa',
    borderRadius: 5,
  },
  '&::-webkit-scrollbar-track': {
    backgroundColor: 'transparent',
  },
})

const ViewBox = styled(Box)({
	flex: 1,
	height: "100%",
	border: `1px solid ${COLOR.GRAY}`,
	borderRadius: 10,
	padding: 10,
  paddingRight:5,
	boxSizing: "border-box",

});

const WriteBox = styled(Box)({
  flex: 1,
	height: "100%",
	boxSizing: "border-box",
  borderRadius: 10,
  padding: 10,
  paddingRight:5,
	border: `1px solid ${COLOR.GRAY}`,


  '&>textarea::-webkit-scrollbar': {
    width: 5, 
  },
  '&>textarea::-webkit-scrollbar-thumb': {
    background: '#aaa',
    borderRadius: 5,
  },
  '&>textarea::-webkit-scrollbar-track': {
    backgroundColor: 'transparent',
  },
});

// ì œëª©, ì´ì–´ì“°ê¸° ë²„íŠ¼ ì˜ì—­
const HeaderBox = styled(Box)({
	width: "100%",
	display: "flex",
	boxSizing: "border-box",
});

// ê¸€ììˆ˜ í‘œì‹œ ì˜ì—­
const CountText = styled(Typography)({
  fontSize: 13,
  margin: "3px 5px 0px auto"
});


const Content = styled(Typography)({
	fontSize: 15,
  marginRight:5
});

const writeNovText = (color) => {
  return (
    {
      border:'none',
      width: "100%",
      height: "100%",
      boxSizing: "borderBox",
      resize: "none",
      outline: "none",
      fontSize: 17,
      boxSizing: "border-box",
      backgroundColor: color,
      color: color === "#121212" ? "white" : "black",
    }
  )
};

/** ì„œë¸Œ ì†Œì„¤ title, content ì‘ì„± ì»´í¬ë„ŒíŠ¸ (ë¯¸ì™„ì„± ì†Œì„¤ ë³´ëŠ” íŒì—…ì—ì„œ ì´ì–´ì“°ê¸° ë²„íŠ¼ í´ë¦­ ì‹œ í•´ë‹¹ íŒì—… ë„ì›Œì¤Œ) */
const WriteSubNovModal = (props) => {
	const [title, setTitle] = useState("");           // ì„œë¸Œ ì†Œì„¤ ì œëª©
	const [content, setContent] = useState("");       // ì„œë¸Œ ì†Œì„¤ ë‚´ìš©
  // const [isScrolling, setIsScrolling] = useState(false);
  const [contentCount, setContentCount] = useState(0) // ë‚´ìš© ê¸€ììˆ˜ ì²´í¬

	const inputTitle = (e) => {
		setTitle(e.target.value);
	};

	const inputContent = (e) => {
		setContent(e.target.value);
    setContentCount(e.target.value.length);

	};

  // ì €ì¥ í›„ ë‹¤ìŒ ë²„íŠ¼ ëˆŒë €ì„ ë•Œ NovDetail í˜ì´ì§€ì— ìˆëŠ” (ì„œë²„ë¡œ ë³´ë‚¼) ìƒíƒœê°’ì— ë°ì´í„° ì„¸íŒ…
	const postSubNovel = () => {
		if (content === "") {
			alert(MESSAGE.ERROR.WRITE_CONTENT);
			return;
		} else if (title.length > 50) {
			alert(MESSAGE.ERROR.TITLE_INVALIDATION);
			return;
		} else {
			props.setTitleContent({
				title: title,
				content: content,
				main_novel_seqno: props.mainNovel.main_seqno,
			});
		}
	};

  //ìŠ¤í¬ë¡¤ ê°ì§€í•˜ì—¬ í™”ë©´ì— ë„ì›Œì£¼ëŠ” í•¨ìˆ˜
  // const handleScroll = () => {
  //   setIsScrolling(true);
    // ìŠ¤í¬ë¡¤ë§ 0.5ì´ˆ í›„ ë™ì‘ ë©ˆì¶¤
  //   setTimeout(() => {
  //     setIsScrolling(false);
  //   }, 1000);
  // }
  
  // useEffect(() => {
  //   const temp = document.querySelector('.cc')
  //   temp.addEventListener("scroll", handleScroll);

    // í´ë¦°ì—… (ì–¸ë§ˆìš´íŠ¸ ì‹œ ì´ë²¤íŠ¸ ë¦¬ìŠ¤ë„ˆ ì‚­ì œ)
  //   return () => {
  //     temp.removeEventListener("scroll", handleScroll);
  //   };
  // }, []);

	return (
		<Wrapper>
			<HeaderBox>
				<Inputs
					fullWidth
					defaultValue={props.mainNovel.title}
					onChange={inputTitle}
					color={props.color}
				/>
				<Buttons
					type={CODE.BUTTON.BASIC}
					color={props.color === "#121212" ? COLOR.WHITE : COLOR.BLACK}
					name={LABEL.BUTTONS.GOTONEXT}
					margin={"-20px 0px 0px auto"}
					postSubNovel={postSubNovel}
					changeState={(content !== "" && title.length <= 50) && props.changeState}
				/>
			</HeaderBox>
			<WholeBox>
				<ViewBox>
					<ScrollBox>
						<Content>
							{props.mainNovel.content && props.mainNovel.content
								? props.mainNovel.content.split("\\n").map((line, i) => (
										<div key={i}>
											{line.replace("\\r", "")}
											<br />
										</div>
								  ))
								: ""}
						</Content>
					</ScrollBox>
				</ViewBox>
				<WriteBox>
					<textarea
						style={writeNovText(props.color)}
						onChange={inputContent}
						maxLength={10000}
					/>
				</WriteBox>
			</WholeBox>
			{contentCount === 10000 ? (
				<CountText style={{ color: "red" }}>{contentCount}/10000</CountText>
			) : (
				<CountText>{contentCount}/10000</CountText>
			)}
		</Wrapper>
	);
};

export default WriteSubNovModal;