/** ì™„ì„± ì‘í’ˆ ì½ê¸° ì»´í¬ë„ŒíŠ¸ (ì‘í’ˆ ìƒì„¸ í˜ì´ì§€ì—ì„œ view ë²„íŠ¼ í´ë¦­ ì‹œ ì™„ì„±ì‘ì¼ ê²½ìš° í•´ë‹¹ íŒì—… ë„ì›Œì¤Œ) */
const ViewCompleteNovModal = (props) => {

	const [completeNovel, setCompleteNovel] = useState({});   // ì™„ì„± ì†Œì„¤ ë°ì´í„°

	// ì™„ì„± ì†Œì„¤ ë³´ê¸°
	useEffect(() => {
		getData("novel/getCompleteNovel", { complete_seqno: props.complete_seqno })
			.then(function (data) {
				setCompleteNovel(data);
			})
			.catch((err) => {
				console.log(err);
			});
	}, []);

	return (
		<Wrapper>
			<Title>{completeNovel[0] && completeNovel[0].complete_novel_title}</Title>
			<ContentBox>
				<Content>
					{completeNovel[0] &&
						completeNovel[0]
							// .complete_novel_content
							// .split("\\n")
							// .map((line, i) => (
							// 	<>
							// 		{line.replaceAll('\\', "")}
							// 		<br />
							// 	</>
							// ))
              .complete_novel_content
							.replaceAll(/(?:\r|\n\|r\n|\\n|\\r|\\n\\n|\\r\\n)/g, "\r\n")
              .replaceAll("\\", "")
              // .split("\r\n")
							// .map((line, i) => (
							// 	<>
							// 		{line.replaceAll("\r\n", <br />)}
							// 		{/* <br /> */}
							// 	</>
							// ))
							}
				</Content>
			</ContentBox>
		</Wrapper>
	);
};

export default ViewCompleteNovModal;