const AuthorBox = styled(Box)({
	width: "100%",
	minHeight: 16,
});

// ì†Œì„¤ ì†Œê°œ ì˜ì—­
const DescriptionBox = styled(Box)({
	flexGrow: 1,
	marginTop: 12,
	marginBottom: 15,
});

// ì œëª© í…ìŠ¤íŠ¸
const Title = styled(Typography)({
	fontSize: 23,
	fontWeight: 800,
});

// ì‘ê°€ ì´ë¦„ í…ìŠ¤íŠ¸
const Author = styled(Typography)({
	fontSize: 15,
	"&:hover": {
		cursor: "pointer",
		fontWeight: 900,
	},
});

// ì†Œì„¤ ì†Œê°œ í…ìŠ¤íŠ¸
const Description = styled(Typography)({
	fontSize: 13,
	color: COLOR.GRAY,
});

// ê¸°í•œ í‘œì‹œ í…ìŠ¤íŠ¸
const DateInfo = styled(Typography)({
	fontSize: 23,
	marginTop: 30,
	marginBottom: 10,
	marginLeft: "auto",
});

//  view ë²„íŠ¼ ì˜ì—­
const NovelViewBox = styled(Box)({
	flex: 1,
	display: "flex",
	width: 70,
	marginLeft: "auto",
});

/** ì†Œì„¤ ì¶•ì•½ ì •ë³´ ì»´í¬ë„ŒíŠ¸(ì†Œì„¤ ìƒì„¸ë³´ê¸° í˜ì´ì§€ì˜ í—¤ë”) */
const NovelInfo = (props) => {

	return (
    <Whole>
      <InfoBox>
        <TitleBox>
          <Title>{props.title}</Title>
        </TitleBox>
        <AuthorBox>
          {props.main_author_id ? (
            <>
              <Author
                onClick={() => {
                  props.showModal();
                  props.setModal("authorDetail");
                  props.setAuthorId(props.main_author_id);
                  props.setAuthorNickName(props.main_author_nickname);
                }}
              >
                By.{props.main_author_nickname}
              </Author>
              {props.sub_author_id && (
                <Author
                  onClick={() => {
                    props.showModal();
                    props.setModal("authorDetail");
                    props.setAuthorId(props.sub_author_id);
                    props.setAuthorNickName(props.sub_author_nickname);
                  }}
                >
                  By.{props.sub_author_nickname}
                </Author>
              )}
            </>
          ) : (
            <Author
              onClick={() => {
                props.showModal();
                props.setModal("authorDetail");
                props.setAuthorId(props.created_user);
                props.setAuthorNickName(props.user_nickname);
              }}
            >
              By.{props.user_nickname}
            </Author>
          )}
        </AuthorBox>
        <DescriptionBox>
          {/* ì‚¬ìš©ìê°€ ì¤„ë°”ê¿ˆì„ í–ˆì„ ë•Œ ê³µë°±ìœ¼ë¡œ ë„ì›Œì£¼ê¸° ìœ„í•´ replace ë§¤ì„œë“œ ì‚¬ìš© */}
          <Description>
            {props.description && props.description.replace(/\\n/g, " ")}
          </Description>
        </DescriptionBox>
      </InfoBox>

      <DateBox>
        {/* {props.complete_seqno ? (
          <DateInfo>{MESSAGE.DDAY_COMPLETE}</DateInfo>
        ) : ( */}
          <DateInfo>
            {/* {MESSAGE.DDAY_COUNT} */}
            {props.novelDdayCounter}
          </DateInfo>
        {/* )} */}
        <NovelViewBox
          onClick={() => {
            props.showModal();
            props.complete_seqno !== undefined
              ? props.setModal("viewComNov")
              : props.setModal("viewIncomNov");
          }}
        >
          <Icons type={CODE.ICON.SEARCH} />
          <Buttons
            type={CODE.BUTTON.BASIC}
            name={LABEL.BUTTONS.VIEWNOVEL}
            height={10}
            margin={"5px 0 0 -12px"}
          />
        </NovelViewBox>
      </DateBox>
    </Whole>
  );
};

export default NovelInfo;
