// React Package Module
import { useState, useRef, useMemo } from "react";

// MUI Package Module
import { Box, styled, Typography } from "@mui/material";

// Control Component
import Buttons from "components/controls/Button";

// Constant
import { CODE, LABEL, COLOR, MESSAGE } from "common";

/** STYLE ì •ì˜ */
// ì „ì²´ ì˜ì—­
const Wrapper = styled(Box)({
  width: "100%",
  height: "100%",
  display: "flex",
  flexDirection: "column",
  boxSizing: "border-box",
});

const ImgBox = styled(Box)({
  width: "100%",
  height: 500,
  boxSizing: "border-box",
  marginBottom: 5,
  display: "flex",
  flexDirection: "column",
});

const CoverImg = styled(Box)({
  width: 270,
  height: 320,
  display: "flex",
  flexDirection: "column",
  boxSizing: "border-box",
  backgroundColor: COLOR.GREEN_TEA,
  margin: "8px auto",
  borderRadius: 20,
});

const ImgDescription = styled(Typography)({
  fontSize: 11,
  color: COLOR.GRAY,
  fontWeight: "bold",
  margin: "0 auto",
});

const IntroMsg = styled(Typography)({
  fontSize: 18,
  fontWeight: "bolder",
  textAlign: "center",
  marginBottom: 20,
});

const fileUploaderBtn = (color) => {
  return {
    backgroundColor: "transparent",
    color: color === "#121212" ? "#ffffff" : "#121212",
    border: "none",
    cursor: "pointer",
    display: "block",
    margin: "0 auto",
    fontSize: 15,
  };
};

/** ì„œë¸Œ ì†Œì„¤ ì‘ì„± í›„ ì†Œì„¤ ì»¤ë²„ ì´ë¯¸ì§€ ì—…ë¡œë“œ í•˜ëŠ” íŒì—… */
const SetNovCoverModal = (props) => {
  const [selectedFileName, setSelectedFileName] = useState(""); // ì‚¬ìš©ìê°€ ì„ íƒí•œ í”„ë¡œí•„ ì‚¬ì§„ ì´ë¦„
  const [previewUrl, setPreviewUrl] = useState(""); // ì„ íƒí•œ ì´ë¯¸ì§€ Blobë°ì´í„°ì˜ ê²½ë¡œ state

  const imageUrl = useMemo(() => {
    if (previewUrl !== "") {
      return previewUrl;
    } else {
      return `${process.env.REACT_APP_COVER_IMAGE_DIRECTORY}/cover_basic.jpg`;
    }
  }, [previewUrl]);

  // íŒŒì¼ ì—…ë¡œë“œ í•˜ëŠ” inputì— ëŒ€í•œ ì°¸ì¡°ë¥¼ ì €ì¥í•˜ëŠ” ë³€ìˆ˜ (í˜„ì¬ ì°¸ì¡° ëŒ€ìƒì´ ì—†ìŒì„ ë‚˜íƒ€ë‚´ê¸° ìœ„í•´ ì´ˆê¸°ê°’ nullë¡œ ì„¸íŒ…)
  const fileInputRef = useRef(null);

  const fileUploadBtn = () => {
    fileInputRef.current.click();
  };

  const fileUpload = (e) => {
    const file = e.target.files[0];

    props.setCoverImage({ file: file }); // NovDetail í˜ì´ì§€ì˜ redgitNovDataì— ì´ë¯¸ì§€ ë°ì´í„° ì„¸íŒ…
    setSelectedFileName(file.name); // ì„ íƒí•œ íŒŒì¼ëª… ì—…ë°ì´íŠ¸

    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setPreviewUrl(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  return (
    <Wrapper>
      <Buttons
        type={CODE.BUTTON.BASIC}
        backgroundColor={COLOR.WHITE}
        color={props.color === "#121212" ? COLOR.WHITE : COLOR.BLACK}
        name={LABEL.BUTTONS.SUBMIT}
        width={50}
        margin={"-10px 35px 5px auto"}
        postSubNovData={props.postSubNovData}
      />
      <IntroMsg>{MESSAGE.SELECT_COVER_INTRO}</IntroMsg>
      <ImgBox>
        <ImgDescription>{MESSAGE.BASIC_COVER_INTRO}</ImgDescription>
        <CoverImg
          style={{
            backgroundImage: `url(${imageUrl})`,
            backgroundSize: "100% 100%",
          }}
        />
        <input
          type="file"
          accept="image/jpg, image/jpeg, image/png"
          onChange={(e) => fileUpload(e)}
          style={{ display: "none" }}
          ref={fileInputRef}
        />
        {/* ì‚¬ìš©ìê°€ ì„ íƒí•œ íŒŒì¼ëª… */}
        {selectedFileName && (
          <p style={{ fontSize: 12, textAlign: "center", margin: "0 auto" }}>
            {selectedFileName}
          </p>
        )}
        {/* ì»¤ìŠ¤í…€í•œ íŒŒì¼ ì—…ë¡œë” ë²„íŠ¼ */}
        <button onClick={fileUploadBtn} style={fileUploaderBtn(props.color)}>
          {LABEL.BUTTONS.UPLOAD_IMAGE}
        </button>
      </ImgBox>
    </Wrapper>
  );
};

export default SetNovCoverModal;
