// React Package Module
import { useState } from "react";

// MUI Package Module
import { Box, styled, Typography } from "@mui/material";

// Control Component
import Buttons from "components/controls/Button";

// Constant
import { CODE, LABEL, COLOR, MESSAGE } from "common";

/** STYLE ì •ì˜ */
// ì „ì²´ ì˜ì—­
const Wrapper = styled(Box)({
	width: "100%",
	height: "100%",
	display: "flex",
	flexDirection: "column",
	padding: "0 3%",
	boxSizing: "border-box",
	// paddingTop: 40,
  paddingBottom: 20,
  //  boxShadow: "inset 0 0 3px 3px blue"

});

//textarea ì˜ì—­
const writeNovText = (color) => {
  return (
    {
      width: "100%",
      height: "100%",
      boxSizing: "borderBox",
      resize: "none",
      outline: "none",
      borderRadius: 10,
      padding: 10,
      fontSize: 17,
      boxSizing: "border-box",
      backgroundColor: color,
      color: color === "#121212" ? "white" : "black"
    }
  )
};

const IntroMsg = styled(Typography)({
	fontSize: 18,
	fontWeight: "bolder",
	textAlign: "center",
	marginBottom: 30,
});

/** ì„œë¸Œ ì†Œì„¤ ì‘ì„± í›„ ì†Œì„¤ ì„¤ëª…ì„ ì‘ì„±í•˜ëŠ” ì»´í¬ë„ŒíŠ¸ */
const WriteNovIntroModal = (props) => {
	const [description, setDescription] = useState("");     // ì„œë¸Œ ì†Œì„¤ ì„¤ëª…

	const inputDescription = (e) => {
		setDescription(e.target.value);
	};

	// ì €ì¥ í›„ ë‹¤ìŒ ë²„íŠ¼ ëˆŒë €ì„ ë•Œ NovDetail í˜ì´ì§€ì— ìˆëŠ” (ì„œë²„ë¡œ ë³´ë‚¼) ìƒíƒœê°’ì— ë°ì´í„° ì„¸íŒ…
	const postSubDesc = () => {
    if (description.length < 100) {
      props.setDescription({
        description: description,
      });
      return;
    } else if (description.length === 0) {
      alert(MESSAGE.ERROR.WRITE_DESCRIPTION);
      return;
    }
    alert(MESSAGE.ERROR.DESC_INVALIDATION);
    return;
  };

	return (
		<Wrapper>
			<Buttons
				type={CODE.BUTTON.BASIC}
				backgroundColor={COLOR.WHITE}
				color={props.color === "#121212" ? COLOR.WHITE : COLOR.BLACK}
				name={LABEL.BUTTONS.GOTONEXT}
				margin={"5px 20px 5px auto"}
				postSubDesc={postSubDesc}
				changeState={description.length < 100 && props.changeState}
			/>
			<IntroMsg>{MESSAGE.WRITE_MAIN_NOVEL_INTRO}</IntroMsg>
			<textarea 
				style={writeNovText(props.color)} 
				onChange={inputDescription} 
      />
		</Wrapper>
	);
};

export default WriteNovIntroModal;