// React Package Module
import { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";

// MUI Package Module
import { Box, Typography, styled } from "@mui/material";

// Content Component
import NovelInfo from "components/contents/NovelInfo";

// Layout Component
import BasicTable from "components/layout/BasicTable";

// Control Component
import Buttons from "components/controls/Button";

// Modal Component
import ModalModal from "components/popup/ModalModal";
import ViewCompleteNovModal from "components/popup/ViewCompleteNovModal";
import ViewIncompleteNovModal from "components/popup/ViewIncompleteNovModal";
import WriteSubNovModal from "components/popup/WriteSubNovModal";
import AuthorDetailModal from "components/popup/AuthorDetailModal";
import SelectTagModal from "components/popup/SelectTagModal";
import WriteNovIntroModal from "components/popup/WriteNovIntroModal";
import SetNovCoverModal from "components/popup/SetNovCoverModal";
import ViewSubNovModal from "components/popup/ViewSubNovModal";

// Constant
import { CODE, LABEL, MESSAGE } from "common";

// util
import {
	modalWidth,
	modalHeight,
	modalColorMode,
	compressImage,
} from "common/util";

// API
import { getData, postData } from "common/communication";

/** ì˜ì—­ STYLE ì •ì˜ */
// ì „ì²´ ì˜ì—­
const Wrapper = styled(Box)({
	width: "99vw",
	height: "99vh",
	display: "flex",
	flexDirection: "column",
	padding: "0px 100px",
	boxSizing: "border-box",
});

// ì†Œì„¤ ì»¤ë²„ ì´ë¯¸ì§€, ì†Œì„¤ ëª©ë¡ ê²Œì‹œíŒ ì˜ì—­
const NovDetailBox = styled(Box)({
	height: "100%",
	display: "flex",
	flexWrap: "wrap",
});

// ì†Œì„¤ ì»¤ë²„ ì´ë¯¸ì§€ ì˜ì—­
// const NovCoverBox = styled(Box)({
// 	marginRight: 20,
// });

// ì†Œì„¤ ì»¤ë²„ ì´ë¯¸ì§€ ì˜ì—­
const NovelCover = styled(Box)({
	width: 200,
	height: 300,
	marginTop: 5,
	borderRadius: 15,
	backgroundColor: "pink",
	backgroundSize: "cover",
	backgroundRepeat: "no-repeat",
});

// ì†Œì„¤ ê²Œì‹œíŒ ì˜ì—­
const NovBoardBox = styled(Box)({
	height: "70%",
	display: "flex",
	flexDirection: "column",
	padding: "10px",
});

// ì†Œì„¤ ê²Œì‹œíŒ ê°¯ìˆ˜ ë° í•„í„° ë²„íŠ¼ ì˜ì—­
const NovBoardInfoBox = styled(Box)({
	height: 20,
	display: "flex",
	marginBottom: 10,
});

// ì†Œì„¤ ê²Œì‹œíŒ ê°¯ìˆ˜
const NovelCount = styled(Typography)({
	height: "5%",
	marginRight: "auto",
	marginLeft: 10,
});

// ì†Œì„¤ ê²Œì‹œíŒ í•„í„° ë²„íŠ¼ ì˜ì—­
const FilterBox = styled(Box)({
	marginRight: 10,
});

/** ì‘í’ˆ ìƒì„¸ í˜ì´ì§€ (NovelCard ì»´í¬ë„ŒíŠ¸ í´ë¦­ ì‹œ í•´ë‹¹ í˜ì´ì§€ë¡œ ì´ë™) */
const NovDetail = () => {
	// ë©”ì¸ í˜ì´ì§€ì—ì„œ ë„˜ê²¨ë°›ì€ í´ë¦­í•œ ì†Œì„¤ì˜ ìƒì„¸ ì •ë³´
	// navigate ë©”ì„œë“œë¡œ ë„˜ê¸´ propsë¥¼ ë°›ëŠ” ë°©ë²•
	const location = useLocation();

	const novel = location.state.props;

	/** STATE ì •ì˜
	 * modal: Modal íŒì—… ìƒíƒœ
	 * popup:  íŒì—… ë‚´ìš© ë³€ê²½
	 * authorId: ì†Œì„¤ ì •ë³´ í—¤ë”ì—ì„œ í´ë¦­í•œ ì‘ê°€ ID
	 * authorNickName: ì†Œì„¤ ì •ë³´ í—¤ë”ì—ì„œ í´ë¦­í•œ ì‘ê°€ Nickname
	 * subNovelData: ë©”ì¸ ì†Œì„¤ì— ì—°ê²°ëœ ì„œë¸Œ ì†Œì„¤ë“¤ ë°ì´í„°(BasicTableì— ë„˜ê¹€)
	 * mainNovel: ì´ì–´ì“°ê¸° íŒì—…ì—ì„œ ë„ì›Œì ¸ì•¼ í•  ë©”ì¸ ì†Œì„¤ì˜ ë°ì´í„°(title, content, seqnoê°€ ë‹´ê²¨ìˆìŒ)
	 * isPopular: ì„œë¸Œ ì†Œì„¤ì„ ë‹´ì€ tableì— ì¸ê¸°ìˆœì¸ì§€ ìµœì‹ ìˆœì¸ì§€ ì•Œë ¤ì£¼ê¸° ìœ„í•œ ìƒíƒœ
	 * regditNovData: ì´ì–´ì“°ê¸° íŒì—…ì—ì„œ ì…ë ¥í•œ ì„œë¸Œì†Œì„¤ ë°ì´í„°(t_sub_novel_mgt í…Œì´ë¸”ì— ë“¤ì–´ê°ˆ ë°ì´í„°)
	 */
	const [modal, setModal] = useState(false); // ëª¨ë‹¬ open ì—¬ë¶€
	const [popup, setModal] = useState(""); // popup ìƒíƒœê°’
	const [authorId, setAuthorId] = useState(""); // ì†Œì„¤ ì •ë³´ í—¤ë”ì—ì„œ í´ë¦­í•œ ì‘ê°€ ì•„ì´ë””
	const [authorNickName, setAuthorNickName] = useState(""); // ì†Œì„¤ ì •ë³´ í—¤ë”ì—ì„œ í´ë¦­í•œ ì‘ê°€ ë‹‰ë„¤ì„
	const [subNovelData, setSubNovelData] = useState([]); // ë©”ì¸ ì†Œì„¤ì— ì—°ê²°ëœ ì„œë¸Œ ì†Œì„¤ë“¤ ë°ì´í„°(BasicTableì— ë„˜ê¹€)
	const [mainNovel, setMainNovel] = useState({}); // ì´ì–´ì“°ê¸° íŒì—…ì—ì„œ ë„ì›Œì ¸ì•¼ í•  ë©”ì¸ ì†Œì„¤ì˜ ë°ì´í„°(title, content, seqnoê°€ ë‹´ê²¨ìˆìŒ)
	const [novelIdx, setNovelIdx] = useState(0); // í´ë¦­í•œ ì†Œì„¤ì˜ ì¸ë±ìŠ¤
	const [isPopular, setIsPopular] = useState(false); // ì„œë¸Œ ì†Œì„¤ì„ ë‹´ì€ tableì— ì¸ê¸°ìˆœì¸ì§€ ìµœì‹ ìˆœì¸ì§€ ì•Œë ¤ì£¼ê¸° ìœ„í•œ ìƒíƒœ
	const [profile, setProfile] = useState(
		JSON.parse(localStorage.getItem("profile"))
	); // ë¡œì»¬ìŠ¤í† ë¦¬ì§€ì— ì €ì¥ëœ ì‚¬ìš©ì ì •ë³´
	const [isLike, setIsLike] = useState(false); // ë…¸ë²¨ì¹´ë“œì—ì„œ ì†Œì„¤ ì°œ ìƒíƒœ ë³€ê²½ ì—¬ë¶€ ê³µìœ ë°›ê¸° ìœ„í•œ ìƒíƒœ
	const [color, setColor] = useState("#ffffff"); // ë°°ê²½ìƒ‰ ëª¨ë“œ

	// ì„œë²„ì— postí•˜ê¸° ìœ„í•œ ë°ì´í„°
	const [regditNovData, setRegditNovData] = useState({
		main_novel_seqno: null,
		title: null,
		content: null,
		genre_1: null,
		genre_2: null,
		keyword_1: null,
		keyword_2: null,
		keyword_3: null,
		description: null,
		file: "cover_basic.jpg",
		created_user: profile.login_id,
	});

	// ì¸ê¸°ìˆœ (like_countê°€ ë§ì€ ìˆœìœ¼ë¡œ ì •ë ¬í•  ì„œë¸Œ ì†Œì„¤ ë°°ì—´)
	const [popularOrder, setPopularOrder] = useState([]);

	// ìœ„ popularOrder ë°°ì—´ ì´ˆê¹ƒê°’ìœ¼ë¡œ subNovelDataë¥¼ ë„£ì—ˆë”ë‹ˆ ë°˜ì˜ë˜ì§€ ì•ŠëŠ” ë¬¸ì œ ë°œìƒí•˜ì—¬ useEffectë¡œ ì²˜ë¦¬
	useEffect(() => {
		setPopularOrder([...subNovelData]);
	}, [subNovelData]);

	// ì¸ê¸°ìˆœ Filtering
	const sortPopular = () => {
		setIsPopular(true);
		popularOrder.sort((a, b) => b.sub_like_count - a.sub_like_count);
	};

	// ì†Œì„¤ì— ë”¸ë¦° ì„œë¸Œ ì†Œì„¤ ê°€ì ¸ì˜¤ê¸°
	const getSubNovelData = () => {
		getData("novel/getSubNovel", {
			main_novel_seqno: novel.main_seqno,
			user_id: profile.login_id,
		})
			.then(function (data) {
				setSubNovelData(data);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	// ì„œë¸Œì†Œì„¤ ë°ì´í„° ì¡°íšŒ
	useEffect(() => {
		getSubNovelData();
	}, [novel.main_seqno]);

	/** Modal OPEN/CLOSE */
	const showModal = () => {
		setModal(true);
	};
	const closeModal = () => {
		setModal(false);
	};

	/** ê° íŒì—…ì—ì„œ ë°›ì•„ì˜¨ ë°ì´í„°ë¥¼ NovDetail í˜ì´ì§€ stateì— ì„¸íŒ… */
	// WriteSubNovModal ì…ë ¥í•˜ì—¬ ë°›ì•„ì˜¨ title, content ì„¸íŒ… í•¨ìˆ˜
	const setTitleContent = (data) => {
		setRegditNovData((prevState) => ({
			...prevState,
			title: data.title === "" ? novel.title : data.title, // ì„œë¸Œ ì†Œì„¤ íƒ€ì´í‹€ì´ ë³€ê²½ë˜ì§€ ì•Šì•˜ì„ ê²½ìš° ë©”ì¸ ì†Œì„¤ íƒ€ì´í‹€ ë°ì´í„° ì„¸íŒ…
			content: data.content,
			main_novel_seqno: data.main_novel_seqno,
		}));
	};

	// SelectTagModalì—ì„œ ë°›ì•„ì˜¨ genre, keyword ì„¸íŒ… í•¨ìˆ˜
	const setTags = (data) => {
		setRegditNovData((prevState) => ({
			...prevState,
			genre_1: data.genre[0].code,
			genre_2: data.genre[1] && data.genre[1].code,
			keyword_1: data.keyword[0].code,
			keyword_2: data.keyword[1] && data.keyword[1].code,
			keyword_3: data.keyword[2] && data.keyword[2].code,
		}));
	};

	// WriteNovIntroModal ë°›ì•„ì˜¨ description ì„¸íŒ… í•¨ìˆ˜
	const setDescription = (data) => {
		setRegditNovData((prevState) => ({
			...prevState,
			description: data.description,
		}));
	};

	// SelectTagModal ë°›ì•„ì˜¨ description ì„¸íŒ… í•¨ìˆ˜
	const setCoverImage = (data) => {
		setRegditNovData((prevState) => ({
			...prevState,
			file: data.file,
		}));
	};

	// ì„œë¸Œ ì†Œì„¤ ë°ì´í„° ì„œë²„ ì „ì†¡
	const postSubNovData = async () => {
    let subNovData;

    // ì»¤ë²„ ì´ë¯¸ì§€ ì„ íƒí•  ê²½ìš° í¼ ë°ì´í„° ìƒì„±
    if (regditNovData.file !== "cover_basic.jpg") {
      // ì„ íƒëœ ì´ë¯¸ì§€ íŒŒì¼ì˜ í™•ì¥ìëª…ì´ jpeg, jpg, pngê°€ ì•„ë‹ ê²½ìš° ê²½ê³ ë¬¸ ë„ìš°ê³  í•¨ìˆ˜ ì¢…ë£Œ
      const ext = regditNovData.file.type.split("/")[1];
      const allowList = ["jpeg", "jpg", "png"];
      if (!allowList.includes(ext)) {
        alert(MESSAGE.ERROR.CHECK_EXT);
        return;
      }

      // ì´ë¯¸ì§€ ì••ì¶•
      const resizingImg = await compressImage(regditNovData.file);
      setRegditNovData((prev) => ({
        ...prev,
        file: resizingImg,
      }));

      const formData = new FormData();

      Object.keys(regditNovData).forEach((key) => {
        formData.append(key, regditNovData[key]);
      });

      subNovData = formData;

      // ì»¤ë²„ ì´ë¯¸ì§€ ì„ íƒì—†ì´ ë‚˜ë¨¸ì§€ ë°ì´í„°ë§Œ ì „ì†¡í•  ê²½ìš°
    } else {
      subNovData = regditNovData;
    }

    postData("novel/postSubNovel", subNovData)
      .then((msg) => {
        alert(msg);
        closeModal();
        getSubNovelData();
      })
      .catch((err) => {
        console.log(err);
      });
  };

	// ì†Œì„¤ ë§ˆê° ê¸°í•œ ì•Œë ¤ì£¼ëŠ” ë””ë°ì´ ì¹´ìš´í„° í•¨ìˆ˜
	const novelDdayCounter = () => {
		// ë¬¸ìì—´ë¡œ ë½‘íˆëŠ” created_dateë¥¼ Date ê°ì²´ë¡œ ë³€í™˜
		const createdDate = new Date(novel.created_date);

		// ë§ˆê°ì¼ì€ ì†Œì„¤ì˜ ì‘ì„±ì¼ ê¸°ì¤€ 30ì¼ í›„ë¡œ ì„¤ì •
		const dueDate = new Date(createdDate.setDate(createdDate.getDate() + 30));

		// í˜„ì¬ ì‹œê°„(PC ì„¤ì • ê´€ê³„ì—†ì´ ì„œìš¸ ì‹œê°„ìœ¼ë¡œ ê³ ì •)
		const krCurr = new Date(
			new Date().getTime() +
				new Date().getTimezoneOffset() * 60 * 1000 +
				9 * 60 * 60 * 1000
		);

		// í˜„ì¬ ë‚ ì§œì™€ ë§ˆê°ì¼ì˜ ì°¨ì´ë¥¼ ë°€ë¦¬ì´ˆ ë‹¨ìœ„ë¡œ ë³€í™˜í•˜ì—¬ ê³„ì‚°
		let leftTime = dueDate.getTime() - krCurr.getTime();

		// ë°€ë¦¬ì´ˆë¥¼ ì¼ ë‹¨ìœ„ë¡œ ë³€í™˜
	  let leftDay = Math.ceil(leftTime / (1000 * 60 * 60 * 24)); // 1ì´ˆ = 1000ë°€ë¦¬ì´ˆ, 1ë¶„ì€ 60ì´ˆ, 1ì‹œê°„ì€ 60ë¶„, 1ì¼ì€ 24ì‹œê°„
    
    if (leftDay === 0) {
      return MESSAGE.D_DAY
    } else if (leftDay < 0 || novel.complete_seqno) {
      return MESSAGE.DDAY_COMPLETE 
    } else {
      return MESSAGE.DDAY_COUNT + leftDay
    }
	};

	// íŒì—… ìƒíƒœê°’ ë³€ê²½
	const popupChange = () => {
		if (popup === "viewComNov") {
			return (
				<ViewCompleteNovModal
					complete_seqno={novel.complete_seqno}
					color={color}
				/>
			);
		} else if (popup === "viewIncomNov") {
			return (
				<ViewIncompleteNovModal
					changeState={() => setModal("writeNov")}
					main_seqno={novel.main_seqno}
					setMainNovel={(novel) => setMainNovel(novel)}
					login_id={profile.login_id}
					color={color}
				/>
			);
		} else if (popup === "writeNov") {
			return (
				<WriteSubNovModal
					mainNovel={mainNovel}
					changeState={() => setModal("selectTag")}
					setTitleContent={(data) => setTitleContent(data)}
					color={color}
					// í˜„ì¬ dataì˜ í˜•íƒœëŠ” WriteSubNovModalì—ì„œ ë°›ì€ { title: title, content: content }
				/>
			);
		} else if (popup === "authorDetail") {
			return (
				<AuthorDetailModal
					authorId={authorId}
					authorNickName={authorNickName}
					closeModal={closeModal}
				/>
			);
		} else if (popup === "selectTag") {
			return (
				<SelectTagModal
					changeState={() => setModal("novIntro")}
					setTags={(data) => setTags(data)}
					color={color}
				/>
			);
		} else if (popup === "novIntro") {
			return (
				<WriteNovIntroModal
					setDescription={setDescription}
					changeState={() => setModal("novCover")}
					color={color}
				/>
			);
		} else if (popup === "novCover") {
			return (
				<SetNovCoverModal
					setCoverImage={(data) => setCoverImage(data)}
					postSubNovData={postSubNovData}
					color={color}
				/>
			);
		} else if (popup === "viewSubNov") {
			return (
				<ViewSubNovModal
					// ì»´í¬ë„ŒíŠ¸ë¡œ ë„˜ê²¨ì¤„ ë•Œ ì„œë¸Œ ì†Œì„¤ ì „ì²´ê°€ ì•„ë‹Œ í´ë¦­í•œ ì†Œì„¤ì˜ ê³ ìœ  index ìƒíƒœê°’ì„ ë„£ì–´ ì¸ë±ì‹± í•˜ì—¬ ë„˜ê¹€
					subNovelData={subNovelData[novelIdx]}
					mainNovel={novel}
					user_id={profile.login_id}
					setIsLike={setIsLike}
					getSubNovelData={getSubNovelData}
					color={color}
				/>
			);
		}
	};



	return (
		<Wrapper>
			{/* ì†Œì„¤ ì •ë³´ í—¤ë” ì˜ì—­ */}
			<NovelInfo
				// ì†Œì„¤ ì •ë³´
				title={
					novel.complete_novel_title ? novel.complete_novel_title : novel.title
				}
				complete_seqno={novel.complete_seqno}
				description={novel.description}
				main_author_id={novel.main_author_id}
				sub_author_id={novel.sub_author_id}
				main_author_nickname={novel.main_author_nickname}
				sub_author_nickname={novel.sub_author_nickname}
				like_count={novel.like_count}
				created_date={novel.created_date}
				created_user={novel.created_user}
				user_nickname={novel.user_nickname}
				// í˜ì´ì§€ ë° íŒì—… ìƒíƒœ ë³€ê²½
				showModal={showModal}
				setModal={(state) => setModal(state)}
				setAuthorId={(id) => setAuthorId(id)}
				setAuthorNickName={(nickName) => setAuthorNickName(nickName)}
				novelDdayCounter={novelDdayCounter()}
			/>

			{/* ì†Œì„¤ ì´ë¯¸ì§€ ë° ì„œë¸Œ ì†Œì„¤ ì •ë³´ ì˜ì—­ */}
			<NovDetailBox>
				{/* <NovCoverBox> */}
				<NovelCover
					style={{
						backgroundImage: `url(${
							process.env.REACT_APP_COVER_IMAGE_DIRECTORY
						}/${encodeURIComponent(novel.cover_image)})`,
					}}
				/>
				{/* </NovCoverBox> */}
				<NovBoardBox>
					<NovBoardInfoBox>
						{subNovelData.length !== 0 ? (
							<NovelCount>
								{subNovelData.length}
								{MESSAGE.BOARD_COUNT}
							</NovelCount>
						) : (
							<NovelCount>{MESSAGE.WRITE_FIRST_NOVEL}</NovelCount>
						)}
						<FilterBox>
							<Buttons
								type={CODE.BUTTON.BASIC}
								name={LABEL.BUTTONS.LATEST}
								height={10}
								sortLatest={() => setIsPopular(false)}
							/>
							<Buttons
								type={CODE.BUTTON.BASIC}
								name={LABEL.BUTTONS.POPULAR}
								height={10}
								sortPopular={sortPopular}
							/>
						</FilterBox>
					</NovBoardInfoBox>
					<BasicTable
						subNovelData={subNovelData}
						changeState={() => setModal("viewSubNov")}
						showModal={showModal}
						// table ì•ˆì˜ indexë¥¼ ê³ ìœ  í‚¤ ê°’ìœ¼ë¡œ ì‚¬ìš©í•˜ê¸° ìœ„í•´ propsë¡œ ì „ë‹¬
						setNovelIdx={setNovelIdx}
						popularOrder={popularOrder}
						isPopular={isPopular}
					/>
				</NovBoardBox>
			</NovDetailBox>

			{/* ëª¨ë‹¬ íŒì—… ì˜ì—­ */}
			<ModalModal
				fullWidth
				open={modal}
				width={modalWidth(popup)}
				onClose={closeModal}
				height={modalHeight(popup)}
				mode={modalColorMode(popup)}
				setColor={(data) => setColor(data)}
				setColorInit={() => setColor("#ffffff")}
			>
				{popupChange()}
			</ModalModal>
		</Wrapper>
	);
};

export default NovDetail;